	        box-shadow:
		0 0 4px rgba(0,0,0,.14),
		0 4px 8px rgba(0,0,0,.28);
	border-radius: 2px;}
	.error .error-header {
		-ms-flex-pack: justify;
		    justify-content: space-between;
		display: -ms-flexbox;
		display: flex; }
		.error .error-header .error-header-left,
		.error .error-header .error-header-right {
			height: 100%;
			-ms-flex-pack: center;
			    justify-content: center;
